
import { defineComponent, inject, reactive } from 'vue'
import type { DataType } from '@/store/home/type'
import { commonRequest } from '@/network/index'
import { ElMessage } from 'element-plus'
import { ElNotification } from 'element-plus'
export default defineComponent({
  name: '',
  directives: {
    focus: {
      mounted(el, bindings) {
        if (bindings.value === 0) {
          el.focus()
        }
      }
    }
  },
  setup(props, { emit }) {
    const passwords = reactive([
      {
        props: '旧密码',
        password: ''
      },
      {
        props: '新密码',
        password: ''
      },
      {
        props: '确认密码',
        password: ''
      }
    ])
    const personData = inject('personData')
    const changeStatus = () => {
      emit('changeStatus')
    }
    const editMyPassword = () => {
      if (
        passwords[0].password === '' ||
        passwords[1].password === '' ||
        passwords[2].password === ''
      ) {
        ElNotification({
          title: '错误',
          message: '密码不能为空',
          type: 'error',
          duration: 1000
        })
      } else {
        if (passwords[1].password !== passwords[2].password) {
          ElNotification({
            title: '错误',
            message: '密码不一致',
            type: 'error',
            duration: 1000
          })
        } else {
          const data = {
            oldPassword: passwords[0].password,
            newPassword: passwords[1].password,
            confirmPassword: passwords[2].password
          }
          commonRequest
            .request<DataType>({
              url: '/tUser/changePassword',
              method: 'POST',
              headers: {
                'Content-Type': 'application/json'
              },
              params: data
            })
            .then((res) => {
              console.log(res)
              if (res.data.status === 200) {
                ElNotification({
                  title: '成功',
                  message: '修改密码成功',
                  type: 'success',
                  duration: 1000
                })
              } else {
                ElNotification({
                  title: 'system',
                  message: '系统异常',
                  type: 'error',
                  duration: 1000
                })
              }
            })
            .catch(() => {
              ElNotification({
                title: 'network',
                message: '网络异常',
                type: 'error',
                duration: 1000
              })
            })
        }
      }
    }
    return {
      changeStatus,
      personData,
      passwords,
      editMyPassword
    }
  }
})
