
import { defineComponent, inject } from 'vue'
import type { DataType } from '@/store/home/type'
import { arrayToObject } from '@/utils/data-transfer'
import { commonRequest } from '@/network/index'
import { ElNotification } from 'element-plus'
export default defineComponent({
  name: '',
  setup(props, { emit }) {
    const personData: any = inject('personData')
    const changeStatus = () => {
      emit('changeStatus')
    }
    const editMyInfo = () => {
      let res = arrayToObject(personData.value)
      commonRequest
        .request<DataType>({
          url: '/tUser/updateInfor',
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          params: res
        })
        .then((res) => {
          console.log(res)
          if (res.data.status === 200) {
            ElNotification({
              title: '成功',
              message: '修改用户信息成功',
              type: 'success',
              duration: 1000
            })
          } else {
            ElNotification({
              title: 'system',
              message: '系统异常',
              type: 'error',
              duration: 1000
            })
          }
        })
        .catch(() => {
          ElNotification({
            title: 'network',
            message: '网络异常',
            type: 'error',
            duration: 1000
          })
        })
    }
    return {
      status,
      changeStatus,
      personData,
      editMyInfo
    }
  }
})
